import React, { useState } from 'react';
import './FlowCalculator.css';

const flowUnits = [
  { value: 'm3h', label: '/', toM3s: v => v / 3600, min: 0.1, max: 10, step: 0.1 },
  { value: 'lmin', label: '/', toM3s: v => v / 60000, min: 1, max: 200, step: 1 },
  { value: 'ls', label: '/', toM3s: v => v / 1000, min: 0.2, max: 20, step: 0.1 },
];

function formatNumber(num) {
  if (isNaN(num)) return '-';
  return num.toLocaleString('ru-RU', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });
}

const FlowByDiameterCalculator = ({ onBack }) => {
  const [velocity, setVelocity] = useState('1');
  const [diameter, setDiameter] = useState('50');
  const [tooltip, setTooltip] = useState(false);

  //  
  let results = {
    m3h: '-',
    lmin: '-',
    ls: '-'
  };

  const v = parseFloat(velocity);
  const d = parseFloat(diameter);
  
  if (!isNaN(v) && !isNaN(d) && d > 0) {
    const dMeters = d / 1000;
    const area = Math.PI * Math.pow(dMeters / 2, 2);
    const flowM3s = v * area;
    
    //    
    results = {
      m3h: formatNumber(flowM3s * 3600),
      lmin: formatNumber(flowM3s * 60000),
      ls: formatNumber(flowM3s * 1000)
    };
  }

  return (
    <div className="container">
      <div className="card" style={{position: 'relative'}}>
        {onBack && (
          <button
            className="back-btn"
            onClick={onBack}
            aria-label=""
            style={{
              position: 'absolute',
              top: 12,
              left: 12,
              width: 40,
              height: 40,
              borderRadius: 10,
              background: 'var(--main-blue)',
              color: '#fff',
              border: 'none',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              boxShadow: '0 2px 8px rgba(76,201,240,0.13)',
              cursor: 'pointer',
              zIndex: 20,
              transition: 'background 0.2s, transform 0.2s',
              padding: 0,
            }}
            onMouseDown={e => e.currentTarget.style.transform = 'scale(0.93)'}
            onMouseUp={e => e.currentTarget.style.transform = 'scale(1)'}
            onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
          >
            <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M15 4L8 11L15 18" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        )}
        <button
          className="info-btn info-btn-square"
          tabIndex={0}
          aria-label=""
          style={{
            position: 'absolute',
            top: 12,
            right: 12,
            width: 40,
            height: 40,
            borderRadius: 10,
            background: 'var(--main-blue)',
            color: '#fff',
            border: 'none',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            boxShadow: '0 2px 8px rgba(76,201,240,0.13)',
            cursor: 'pointer',
            zIndex: 20,
            transition: 'background 0.2s, transform 0.2s',
            padding: 0,
          }}
          onClick={() => setTooltip(!tooltip)}
          onBlur={() => setTooltip(false)}
          onMouseDown={e => e.currentTarget.style.transform = 'scale(0.93)'}
          onMouseUp={e => e.currentTarget.style.transform = 'scale(1)'}
          onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
        >
          <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="11" cy="11" r="10" stroke="white" strokeWidth="2" fill="none"/>
            <rect x="10" y="7" width="2" height="2" rx="1" fill="white"/>
            <rect x="10" y="11" width="2" height="6" rx="1" fill="white"/>
          </svg>
        </button>
        <div className="header-row">
          <div className="card-title"> <br /> </div>
        </div>
        <div className="results-block">
          <div className="result-row">
            <span className="result-label"> </span>
            <span className="result-value">{results.m3h} /</span>
          </div>
          <div className="result-row">
            <span className="result-label"> </span>
            <span className="result-value">{results.lmin} /</span>
          </div>
          <div className="result-row">
            <span className="result-label"> </span>
            <span className="result-value">{results.ls} /</span>
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="velocity"> , /</label>
          <input
            type="number"
            id="velocityNum"
            step="0.01"
            min="0.5"
            max="2"
            value={velocity}
            onChange={e => setVelocity(e.target.value)}
            inputMode="numeric"
          />
          <div className="slider-block">
            <input
              type="range"
              id="velocity"
              min="0.5"
              max="2"
              step="0.01"
              value={velocity}
              onChange={e => setVelocity(e.target.value)}
            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="diameter">  , </label>
          <input
            type="number"
            id="diameter"
            step="1"
            min="10"
            max="50"
            value={diameter}
            onChange={e => setDiameter(e.target.value)}
            inputMode="numeric"
          />
          <div className="slider-block">
            <input
              type="range"
              id="diameter-range"
              min="10"
              max="50"
              step="1"
              value={diameter}
              onChange={e => setDiameter(e.target.value)}
            />
          </div>
        </div>
        <div className={`tooltip${tooltip ? ' active' : ''}`}>
          <strong> :</strong>
          <span className="formula">Q = V  A</span>
          <b>Q</b>   , /<br />
          <b>V</b>   , /<br />
          <b>A</b>    , <br />
          <br />
          <b>:</b>
          <ul style={{margin:0, paddingLeft:20}}>
            <li>     A =   (D/2)</li>
            <li>D      </li>
            <li>      </li>
          </ul>
        </div>
        <div className="footer-signature">Galf Design</div>
      </div>
    </div>
  );
};

export default FlowByDiameterCalculator; 