import React, { useState } from 'react';
import './FlowCalculator.css';

const MenuContainer = (props) => (
  <div className="container" style={{marginTop: 0}}>{props.children}</div>
);

const MenuCard = (props) => (
  <div className="card" style={{marginBottom: 18}}>{props.children}</div>
);

const MainMenu = ({ onShowCalculator }) => {
  const [openMenu, setOpenMenu] = useState(null);

  const toggleMenu = (menuId) => {
    setOpenMenu(openMenu === menuId ? null : menuId);
  };

  const menuItems = {
    hydraulic: {
      title: '',
      items: [
        {
          title: '   ',
          url: '',
          id: 'flowCalculator',
        },
        {
          title: '   ',
          url: '',
          id: 'flowByDiameterCalculator',
        },
        {
          title: ' ',
          url: '',
          id: 'liquidSpeedCalculator',
        },
        {
          title: ' ',
          url: '',
          id: 'hydraulicResistanceCalculator',
        },
      ],
    },
    heating: {
      title: '',
      items: [
        {
          title: ' ',
          url: '',
          id: 'pumpSelectionCalculator',
        },
        {
          title: '  ()',
          url: '',
          id: 'radiatorCalculator',
        },
      ],
    },
  };

  return (
    <MenuContainer>
      <div className="header-row" style={{
        justifyContent: 'center',
        marginBottom: 24,
        textAlign: 'center'
      }}>
        <span style={{
          fontWeight: 700,
          fontSize: 24,
          color: '#8a8a8a',
          letterSpacing: '0.04em',
          fontFamily: "'Roboto', sans-serif"
        }}>
          Galf Design 
        </span>
      </div>
      {Object.entries(menuItems).map(([key, menu]) => (
        <MenuCard key={key}>
          <div className="toggle-row" style={{marginBottom: 0}}>
            <button
              className={`toggle-option${openMenu === key ? ' active' : ''}`}
              style={{width: '100%', fontSize: '1.25em', justifyContent: 'space-between'}}
              onClick={() => toggleMenu(key)}
            >
              {menu.title} <span style={{float: 'right', fontSize: '0.9em', marginLeft: 8}}>{openMenu === key ? '' : ''}</span>
            </button>
          </div>
          {openMenu === key && (
            <div style={{marginTop: 10}}>
              {menu.items.map((item) => (
                <button
                  key={item.id}
                  className="toggle-option"
                  style={{
                    width: '100%',
                    textAlign: 'left',
                    marginBottom: 7,
                    fontWeight: 500,
                    fontSize: '1.15em',
                    borderRadius: 10,
                    background: 'var(--toggle-bg)',
                    color: 'var(--input-text)',
                    border: '1.5px solid var(--border)',
                    padding: '12px 16px',
                    transition: 'background 0.18s, color 0.18s',
                  }}
                  onClick={() => onShowCalculator(item.id, item.title, item.url)}
                >
                  {item.title}
                </button>
              ))}
            </div>
          )}
        </MenuCard>
      ))}
    </MenuContainer>
  );
};

export default MainMenu; 